@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Roboto:ital,wght@0,400;0,500;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

*,::before,::after{box-sizing:border-box}body{background-color:#f8fafc}.header{display:flex;align-items:center;justify-content:space-between;padding:20px;background-color:#fff;margin-bottom:20px;grid-gap:20px;gap:20px}.header img{height:40px}.search-input-wrapper{border:1px solid rgba(128,128,128,.3294117647);border-radius:20px;min-width:400px;display:flex;align-items:center;height:40px;padding-left:10px}.search-input-wrapper .search-input{flex:1 1;padding:10px 10px 10px 0}.search-input-wrapper .search-input fieldset{border:none;height:30px}.search-input-wrapper .search-button{background-color:#c63737;height:100%;border-top-right-radius:16px;border-bottom-right-radius:16px;display:grid;align-content:center}.search-input-wrapper .search-button .search-button-text{color:#fff;padding:10px 42px;font-size:14px}.card-item-wrapper{position:relative;min-height:350px;display:flex;flex-direction:column;justify-content:space-between}.discount-rate{position:absolute;top:10px;left:10px;background-color:#c63737;border-radius:28px;padding:5px 15px}.discount-rate .item-discount{font-size:10px;color:#fff}.card-image{width:100%;height:230px;object-fit:contain;background-color:#f3f3f3;padding:30px 0}.card-bottom{display:flex;justify-content:space-between;grid-gap:5px;gap:5px;padding:20px}.card-info{display:flex;flex-direction:column;grid-gap:10px;gap:10px}.card-info .product-name,.card-info .rating-text{font-size:14px}.card-info .rating-text{color:rgba(128,128,128,.5803921569)}.card-info .product-name{font-weight:600}.card-info .rating-box{display:flex;align-items:center;grid-gap:5px;gap:5px}.card-info .price-row{display:flex;align-items:center;grid-gap:10px;gap:10px}.card-info .price-row .price{color:#c63737;font-weight:600;font-size:14px}.card-info .price-row .original-price{color:rgba(128,128,128,.5803921569);font-weight:600;font-size:14px;text-decoration:line-through}.card-buttons{display:flex;flex-direction:column;justify-content:flex-end;align-items:center;grid-gap:20px;gap:20px;height:90px}.card-buttons .quantity{margin:-16px 0}.card-buttons .card-button{width:35px;height:35px;min-width:unset;border-radius:6px;border:1px solid #c63737}.slide-item{padding:0 20px}.no-record{font-weight:600;font-size:16px;text-align:center}.loading-box{width:100%;height:100%;text-align:center}@media(max-width: 768px){.header{flex-direction:column}.search-input-wrapper{min-width:unset;width:100%}.search-input-wrapper input{font-size:14px;padding-left:10px}.search-input-wrapper svg{width:.8em}}
